<template>
  <!-- 标题组件-传入的必须是字符串类型的数据-->
  <div class="title">
    <div class="cont">{{title}}</div>
  </div>
</template>

<script>
export default {
  props: {
    title: String
  }
}
</script>

<style lang="stylus" scoped>
.title {
  width: 204px;
  height: 42px;
  line-height: 20px;
  background-image: url('../../assets/images/home/title.png');
  background-repeat: no-repeat;
  // background-position: center center;
  // background-size: 100% 100%;

  .cont {
    margin-left: 16px;
    font-size: 16px;
    color: #5dd7c3;
    font-weight: bold;
    text-indent: 12px;
    // font-style: oblique; // 字体倾斜
  }
}
</style>



