import { businessConfig } from '@/utils/config'

var database = 'yjzh'

const params = {
  /**
   * 初始化 不初始化，默认businessConfig的数据库 key
   * @param {any} key 数据库key
   */
  init: function (key) {
    database = key
  },
  /**
   * 恢复 默认businessConfig的数据库 key
   *
   */
  initSelf: function () {
    var conf = businessConfig.getConfig()
    database = conf.Database
  },
  /*
   * 切换到用户体系的数据库 key用户下
   **/
  switchYhtx: function () {
    var conf = businessConfig.getConfig()
    database = conf.Yhtxdatabase
  },
  /**
   * 获取 数据库key
   * */
  getDatabase: function () {
    if (database === '') {
      var conf = businessConfig.getConfig()
      return conf.Database
    } else {
      return database
    }
  },
  /**
   * 基础参数对象
   * @param {any} worktype 分类操作 Save Update Delete Get
   * @param {any} tablename 表的别名 例如：t_person 这里传入 person即可，不区分大小写
   * @param {any} tabledata 表字段值动态对象
   * @param {any} conditions  按条件传入集合对象
   * 例如：
   * [{ 'Property': 'Id', 'Oper': 'eq', 'Value': "1" }]
   */
  base: function (worktype, tablename, tabledata, conditions) {
    var item = {
      WorkType: worktype,
      TableName: tablename,
      TableData: tabledata,
      Conditions: conditions
    }
    return item
  },
  /**
   * 基础保存参数对象
   * @param {any} tablename 表的别名 例如：t_person 这里传入 person即可，不区分大小写
   * @param {any} item 对应的表字段对象
   */
  basesave: function (tablename, item) {
    return params.base('Save', tablename, item, null)
  },
  /**
   * 基础更新参数对象
   * @param {any} tablename
   * @param {any} item
   * @param {any} conditions
   */
  baseupdate: function (tablename, item, conditions) {
    return params.base('Update', tablename, item, conditions)
  },
  /**
   * 基础删除参数对象
   * @param {any} tablename
   * @param {any} conditions
   */
  basedelete: function (tablename, conditions) {
    return params.base('Delete', tablename, null, conditions)
  },
  /**
   * 批量参数对象
   * @param {any} list 基础操作集合 添加，修改，删除参数集合
   */
  tranparams: function (list) {
    var table = {}
    table.DataBase = params.getDatabase()
    table.DynamicTableList = list
    return table
  },
  /**
   * 返回保存参数对象
   * @param {any} tablename 表的别名 例如：t_person 这里传入 person即可，不区分大小写
   * @param {any} item  对应的表字段对象
   * 字段别名，不区分大小写 例如：字段s_id 输入：id
   * 操作那个字段就赋值给那个字段，例如如下对象定义：
  * var item = {
        Id :"1",
        Name: "2"
    }
  * 或者
  * var item ={}
      item.Id = "1"
      item.Name = "2"
   */
  save: function (tablename, item) {
    var table = {}
    table.DataBase = params.getDatabase()
    table.DynamicTable = params.basesave(tablename, item)
    return table
  },
  /**
   * 返回更新参数对象
   * @param {any} tablename 表的别名 例如：t_person 这里传入 person即可，不区分大小写
   * @param {any} item 对应的表字段对象
   * 字段别名，不区分大小写 例如：字段s_id 输入：id
   * 操作那个字段就赋值给那个字段，例如如下对象定义：
  * var item = {
        Id :"1",
        Name: "2"
    }
  * 或者
  * var item ={}
      item.Id = "1"
      item.Name = "2"
   *
   * @param {any} conditions 按条件 传入更新条件集合
   * 例如：
   * [{ 'Property': 'Id', 'Oper': 'eq', 'Value': "1" }]
   */
  update: function (tablename, item, conditions) {
    var table = {}
    table.DataBase = params.getDatabase()
    table.DynamicTable = params.baseupdate(tablename, item, conditions)
    return table
  },
  /**
   * 返回删除参数对象
   * @param {any} tablename  表的别名 例如：t_person 这里传入 person即可，不区分大小写
   * @param {any} conditions 按条件传入集合对象
   * 例如：
   * [{ 'Property': 'Id', 'Oper': 'eq', 'Value': "1" }]
   */
  delete: function (tablename, conditions) {
    var table = {}
    table.DataBase = params.getDatabase()
    table.DynamicTable = params.basedelete(tablename, conditions)
    return table
  },
  /**
   * 返回查询参数对象
   * @param {any} tablename  表的别名 例如：t_person 这里传入 person即可，不区分大小写
   * @param {any} fields 暂时保留，以后扩展用
   * @param {any} conditions 按条件传入集合对象
   * 例如：
   * [{ 'Property': 'Id', 'Oper': 'eq', 'Value': "1" }]
   */
  get: function (tablename, fields, conditions) {
    var table = {}
    table.DataBase = params.getDatabase()
    table.DynamicTable = params.base('Get', tablename, null, conditions)
    return table
  },
  /**
   * 返回查询表参数对象
   * @param {any} tablename 表的别名 例如：t_person 这里传入 person即可，不区分大小写
   * @param {any} fields  暂时保留，以后扩展用
   * @param {any} order 排序参数，字段别名，不区分大小写 例如：字段s_id 输入：id
   * @param {any} sort 排序方式 desc 或者 asc
   * @param {any} conditions 按条件传入集合对象
   * 例如：
   * [{ 'Property': 'Id', 'Oper': 'eq', 'Value': "1" }]
   */
  query: function (tablename, fields, order, sort, conditions, type) {
    if (type === undefined || type === null) {
      type = 'Table'
    }
    var table = {}
    table.DataBase = params.getDatabase()
    var para = {}
    para.Type = type
    para.TableView = tablename
    para.Fields = fields
    para.QueryArgs = {
      Order: order,
      Sort: sort,
      Conditions: conditions
    }
    table.DynamicQuery = para
    return table
  },
  /**
   * 返回查询分页参数对象(表)
   * @param {any} tablename  表的别名 例如：t_person 这里传入 person即可，不区分大小写
   * @param {any} fields 暂时保留，以后扩展用
   * @param {any} order 排序参数，字段别名，不区分大小写 例如：字段s_id 输入：id
   * @param {any} sort 排序方式 desc 或者 asc
   * @param {any} conditions 按条件传入集合对象
   * 例如：
   * [{ 'Property': 'Id', 'Oper': 'eq', 'Value': "1" }]
   */
  getListTable: function (tablename, fields, order, sort, conditions) {
    var item = {
      DataBase: params.getDatabase(),
      Type: 'Table',
      TableView: tablename,
      Fields: fields,
      Order: order,
      Sort: sort,
      PageIndex: 1,
      PageSize: 10,
      Conditions: JSON.stringify(conditions)
    }
    return item
  },
  /**
  * 返回查询分页参数对象(视图)
  * @param {any} tablename  表的别名 例如：t_person 这里传入 person即可，不区分大小写
  * @param {any} fields 暂时保留，以后扩展用
  * @param {any} order 排序参数，字段别名，不区分大小写 例如：字段s_id 输入：id
  * @param {any} sort 排序方式 desc 或者 asc
  * @param {any} conditions 按条件传入集合对象
  * 例如：
  * [{ 'Property': 'Id', 'Oper': 'eq', 'Value': "1" }]
  */
  getListView: function (tablename, fields, order, sort, conditions) {
    var item = {
      DataBase: params.getDatabase(),
      Type: 'View',
      TableView: tablename,
      Fields: fields,
      Order: order,
      Sort: sort,
      PageIndex: 1,
      PageSize: 10,
      Conditions: JSON.stringify(conditions)
    }
    return item
  },
  /**
   * 存储过程
   * @param {any} procname 存储过程名
   * @param {any} inparams 输入参数  对象集合[]
   * @param {any} outparams 输出参数  对象集合[]
   */
  procedure: function (procname, inparms, outparms) {
    var table = {}
    table.DataBase = params.getDatabase()
    var para = {
      ProcName: procname,
      InParms: inparms,
      OutParms: outparms
    }
    table.DynamicProcedure = para
    return table
  },
  /**
   * 存储过程分页
   * @param {any} procname 存储过程名
   * @param {any} inparms 输入参数 json字符串
   * @param {any} outparms 输出参数 json字符串
   * @param {any} parmname 参数名字对应 json字符串
   */
  procedurePaging: function (procname, inparms, outparms, parmname) {
    var item = {
      DataBase: params.getDatabase(),
      ProcName: procname,
      InParms: inparms,
      OutParms: outparms,
      ParmNames: parmname
    }
    return item
  },
  /**
   * Execute
   * */
  execUrl: function () {
    var dbconfig = businessConfig.getConfig()
    return dbconfig.Serviceurl + '/base/execute'// 执行url（save update delete）
  },
  /**
  * TranExecute
  * */
  tranExecUrl: function () {
    var dbconfig = businessConfig.getConfig()
    return dbconfig.Serviceurl + '/base/tranexecute'// 执行事务url（save update delete）
  },
  /**
   * Get
   * */
  getUrl: function () {
    var dbconfig = businessConfig.getConfig()
    return dbconfig.Serviceurl + '/base/get'// Geturl
  },
  /**
   * Query
   * */
  queryUrl: function () {
    var dbconfig = businessConfig.getConfig()
    return dbconfig.Serviceurl + '/base/query'// 查询url
  },
  /**
   * GetList
   * */
  getListUrl: function () {
    var dbconfig = businessConfig.getConfig()
    return dbconfig.Serviceurl + '/base/getlist'// 查询url 分页
  },
  /**
   * ExecuteProcedure（通用）
   * */
  execProcedureUrl: function () {
    var dbconfig = businessConfig.getConfig()
    return dbconfig.Serviceurl + '/base/executeprocedure'// ExecuteProcedure 不分页
  },
  /**
   * ExecuteProcedurePaging（分页）
   * */
  execProcedurePageUrl: function () {
    var dbconfig = businessConfig.getConfig()
    return dbconfig.Serviceurl + '/base/executeprocedurepaging'// ExecuteProcedurePaging 分页
  },
  /**
   * UploadFiles 上传附件地址
   * */
  uploadFilesUrl: function () {
    var dbconfig = businessConfig.getConfig()
    return dbconfig.Serviceurl + '/attachment/uploadfiles'// uploadfiles
  },
  /**
   * 下载附件地址
   * */
  downloadFileUrl: function () {
    var dbconfig = businessConfig.getConfig()
    return dbconfig.Serviceurl + '/attachment/downloadfile'// downloadfile
  }
}
const builderParams = params

export { builderParams }



