<template>
  <div class="block">
    <el-timeline>
      <el-timeline-item
        v-for="(activity, index) in activities"
        :key="index"
        :icon="activity.icon"
        :type="activity.type"
        :color="activity.color"
        :size="activity.size"
        :timestamp="activity.timestamp"
        :placement="top"
      >
        <el-card shadow="always" class="mycard">
          <h2 class="name">{{activity.name}}</h2>
          <p class="content" v-html="activity.content"></p>
        </el-card>
        <!-- {{activity.content}} -->
      </el-timeline-item>
    </el-timeline>
  </div>
</template>

<script>
import { builderParams } from '@/utils/builderParams'
import { post, get } from '@/utils/request'
export default {
  data () {
    return {
      top: 'top',
      activities: [{
        name: '',
        content: '',
        timestamp: '事件接报',
        size: 'large',
        color: '#0bbd87',
        type: 'primary',
        icon: 'el-icon-more-outline',
      }, {
        name: '',
        content: '',
        timestamp: '先期处理',
        color: '#0bbd87',
        size: 'large',
        type: 'primary',
        icon: 'el-icon-more-outline'
      }, {
        name: '',
        content: '',
        timestamp: '预案响应',
        size: 'large',
        type: 'primary',
        icon: 'el-icon-more-outline'
      }, {
        name: '',
        content: '',
        timestamp: '指挥调度',
        size: 'large',
        icon: 'el-icon-more-outline',
        type: 'primary',
      }, {
        name: '',
        content: '',
        timestamp: '指挥结束',
        size: 'large',
        icon: 'el-icon-more-outline',
        type: 'primary',
      }]
    }
  },
  props: {
    eventId: {
      type: String,
      default: ''
    }
  },
  methods: {
    filterTime (value) {
      return value.substring(0, value.length - 2)
    },
    async loadEvent () {
      let conditions = [{ Property: 'Id', Oper: 'eq', Value: this.eventId }]
      let item = builderParams.get('Event', '', conditions)
      item.DataBase = 'duty'
      const event = await post({url: builderParams.getUrl(), data: item })
      if (event.Result === 0) {
        if (event.Data.length > 0) {
          var eventInfo = event.Data[0]
          // 事件接报
          this.activities[0].name = eventInfo.Eventtime
          this.activities[0].content = '该事件由[' + eventInfo.Sendunitname + ']发出上报，由[' + eventInfo.Receiveunitname + ']进行受理。'
          // 先期处置(采取措施)
          this.activities[1].name = ''
          var flag2 = 0
          eventInfo.Measure.split('\\n').forEach(item => {
            if (flag2 != 0) {
              this.activities[1].content += '<span style="margin-left:31px;">' + item + '</span><br>'
            } else {
              this.activities[1].content += '<span>' + item + '</span><br>'
            }
            flag2 += 1
          })
          // 指挥结束
          if (eventInfo.Endtime) {
            this.activities[4].name = this.filterTime(eventInfo.Endtime)
            this.activities[4].content = '事件指挥结束'
          }
          // 时间处置状态 (0：事件接报，1先期处置，2预案响应，3指挥调度，4指挥结束)
          if (eventInfo.Disposestate) {
            let state = parseInt(eventInfo.Disposestate)
            for (let index = 0; index < state; index++) {
              this.activities[index].icon = 'el-icon-circle-check'
              this.activities[index].color = '#0bbd87'
            }
          }
        }
      }
      // 预案响应
      let shConditions = [{ Property: 'eventId', Oper: 'eq', Value: this.eventId }]       
      let shItem = builderParams.query('scheme_event', '', 'shStarttime', 'asc', shConditions, 'View')
      const shEventresult = await post({url: builderParams.queryUrl(), data: shItem })
      
      let parmsql = ''
      shEventresult.Data.forEach(item => {
        parmsql += 's_schemeid="' + item.shid + '" or '
      })
      parmsql = parmsql.substring(0, parmsql.length - 3)
      parmsql += ' and s_eventid="' + this.eventId + '"'
      
      let dispatchConditions =[{ Property: 'eventId', Oper: 'sql', Value: parmsql }]  
      let dispatchItem = builderParams.query('Dispatch', '', 'createtime', 'desc', dispatchConditions, 'View')
      const dispatchresult = await post({url: builderParams.queryUrl(), data: dispatchItem})
  
      if (shEventresult.Result === 0 && shEventresult.Data.length > 0 && dispatchresult.Result === 0) {
        var html = ''
        // this.activities[2].name = shEventresult.Data[0].shStarttime.substring(0, shEventresult.Data[0].shStarttime.length - 2)
        var flag1 = 0
        shEventresult.Data.forEach(item => {
          if (flag1 === 0) {
            html += '<span style="font-size:15px;color:#00f1e5;margin-left:-30px;">' + this.filterTime(item.shStarttime) + '</span><br/>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;'
          } else {
            html += '<span style="font-size:15px;color:#00f1e5;">' + this.filterTime(item.shStarttime) + '</span><br/>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;'
          }

          html += item.shcreateunitname + '启动' + item.Shname + '，'
          dispatchresult.Data.forEach(p => {
            if (p.Schemeid === item.shid) {
              if (p.Typename === '企业') {
                html += '派遣组织机构：[' + p.Dispatchtime + ']' + '，'
              }
              if (p.Typename === '专家') {
                html += '派遣' + p.dispatchtype + ':' + p.dispatchname + '，'
              }
              if (p.Typename === '队伍') {
                html += '派遣' + p.dispatchname + '，'
              }
            }
          })
          html += '<br/>'
          flag1 += 1
        })
        this.activities[2].content = html
      }
      // 指挥调度
      // 查询指令
      let cmdConditions = [{ Property: 'caseId', Oper: 'eq', Value: this.eventId }]
      let cmdItem = builderParams.get('cmd', '', cmdConditions)
      const cmdResult = await post({ url: builderParams.getUrl(), data: cmdItem })
      let cmdsql = ''
      cmdResult.Data.forEach(item => {
        cmdsql += ' s_cmdId="' + item.Id + '" or '
      })
      this.activities[3].name = cmdResult.Data[0].Starttime.substring(0, cmdResult.Data[0].Starttime.length - 2)
      cmdsql = cmdsql.substring(0, cmdsql.length - 3)
      // 查询通知反馈
     
      let nofkConditions= [{ Property: 'cmdid', Oper: 'sql', Value: cmdsql }]
      var nofkItem = builderParams.query('zhdd_notifeedback', '', 'fktimestamp', 'asc', nofkConditions, 'View')
      
      const nofkresult = await post({url: builderParams.queryUrl(), data: nofkItem })
      const nofKList = nofkresult.Data
      var zhddhtml = ''
      var notificationIdArr = []
      // 通知的数量
      var notificationList = []

      nofKList.forEach(item => {
        if (notificationIdArr.indexOf(item.notificationId) < 0) {
          notificationIdArr.push(item.notificationId)
          notificationList.push(item)
        }
      })

      cmdResult.Data.forEach(item => {
        notificationList.forEach(no => {
          if (item.Id === no.Cmdid) {
            no.cmdContent = item.Content,
            no.cmdCreateUnitName = item.Createrunitname,
            no.cmdCreateUserName = item.Creatername
          }
        })
      })
      var flag = 0
      notificationList.forEach(item => {
        if (flag != 0) {
          zhddhtml += '<h2 class="name" style="margin-left:-34px;">' + this.filterTime(item.nosendertime) + '</h2>'
        }
        zhddhtml += '<p>' + item.cmdCreateUnitName + '发送任务给' + item.noaccepterunitname + '，内容为：' + item.cmdContent + '</p><br/>'
        nofKList.forEach(p => {
          if (item.notificationId === p.notificationId) {
            if (p.fktimestamp) {
              zhddhtml += '<span style="font-size: 12px;color:#00f1e5;margin-left:20px;">' + this.filterTime(p.fktimestamp) + '</span> <span style="font-size:15px;">' + p.fkcreaterunitname + ' ' + p.fkcreatername + '' + ' 反馈内容：' + p.fkcontent + '</span>'
            }
          }
        })
        flag += 1
      })
      this.activities[3].content = zhddhtml
    }
  },
  mounted () {
    this.loadEvent()
  }
}
</script>

<style lang="stylus">
.block {
  margin: 20px 0 0 20px;

  .mycard {
    .content {
      color: rgb(136, 214, 255);
      font-size: 16px;
      text-indent: 2em;
      letter-spacing: 1px;
      line-height: 20px;
    }

    .name {
      font-size: 15px;
      font-weight: 500;
      color: #00f1e5;
      margin-bottom: 15px;
    }
  }
}

.el-card {
  border: solid 1px #11c2ff;
  background-color: transparent !important;
  // color: #309933;
}

.el-timeline-item__timestamp {
  // color : #14cafa !important ;
}

.el-timeline-item__timestamp.is-top {
  margin-bottom: 8px;
  padding-top: 4px;
  color: #00E9CD;
  font-size: 20px;
}
</style>



