<!--地图组件菜单 模块-->
<template>
  <div class="map">
    <div class="map-tool">
      <div
        class="map-gj"
        @click="resrouceseach1();lefttelescopic();rotates();lefttelescopics();"
        :class="[rotate ? 'fa fa-arrow-down go' : 'fa fa-arrow-down aa']"
      ></div>
      <transition name="fade">
        <div class="shensuo-box" v-if="show">
          <transition name="fade">
            <div class="text">
              <span @click="setViewCenter(117.217713,39.090533,14)">天津市</span>
              <span @click="mapPath()">路径规划</span>
              <span @click="resrouceseach()">资源查询</span>
              <span @click="eventLine">事件回溯</span>
              <span @click="clearLayers">清除地图</span>
            </div>
          </transition>
        </div>
      </transition>
    </div>
    <!-- 资源查询 -->
    <div class="seach">
      <el-collapse-transition>
        <div class="res-seach" v-if="seachshow">
          <div class="sea-input">
            <el-input placeholder="请输入对应名称" v-model="input5" class="input-with-select">
              <el-button slot="append" icon="el-icon-search" @click="output"></el-button>
            </el-input>
          </div>
          <div class="content">
            <el-tabs
              @tab-click="handleClick"
              :tab-position="tabPosition"
              style="height: 200px; border: 1px solid #10507F; width: 450px;"
            >
              <el-tab-pane label="物资" @click="present = 0">
                <el-table
                  :key="Math.random()"
                  :data="tableMaterial"
                  :cell-style="rowstyle"
                  :header-row-style="headerrowstyle"
                  :row-style="cellstyle"
                  :header-cell-style="headstyle"
                  height="200"
                  style="width: 100%"
                  :show-header="showHeader"
                >
                  <el-table-column
                    prop="address"
                    label="名称"
                    :show-overflow-tooltip="true"
                    width="170px"
                  ></el-table-column>
                  <el-table-column prop="usable" label="库存"></el-table-column>
                  <el-table-column label="操作">
                    <template slot-scope="scope">
                      <el-button
                        type="success"
                        size="mini"
                        @click="addresources('goods', scope.row)"
                      >调度</el-button>
                    </template>
                  </el-table-column>
                </el-table>
              </el-tab-pane>
              <el-tab-pane label="专家" @click="present = 1">
                <el-table
                  :key="Math.random()"
                  :data="tableExpert"
                  :cell-style="rowstyle"
                  :header-cell-style="headstyle"
                  height="200"
                  style="width: 100%"
                  :show-header="showHeader"
                >
                  <el-table-column prop="name" label="名称"></el-table-column>
                  <el-table-column prop="expertCode" label="类型" width="120"></el-table-column>
                  <el-table-column label="操作">
                    <template slot-scope="scope">
                      <el-button
                        type="success"
                        size="mini"
                        @click="addresources('expert', scope.row)"
                      >调度</el-button>
                    </template>
                  </el-table-column>
                </el-table>
              </el-tab-pane>
              <!-- <el-tab-pane label="人员" @click="present = 2">人员</el-tab-pane> -->
              <el-tab-pane label="队伍" @click="present = 2">
                <el-table
                  :data="tableTeam"
                  :cell-style="rowstyle"
                  :header-cell-style="headstyle"
                  height="200"
                  style="width: 100%"
                  :show-header="showHeader"
                >
                  <el-table-column prop="name" label="名称"></el-table-column>
                  <el-table-column prop="teamNum" label="总人数"></el-table-column>
                  <el-table-column label="操作">
                    <template slot-scope="scope">
                      <el-button
                        type="success"
                        size="mini"
                        @click="addresources('team', scope.row)"
                      >调度</el-button>
                    </template>
                  </el-table-column>
                </el-table>
              </el-tab-pane>
              <el-tab-pane label="仓库" @click="present = 3">
                <el-table
                  :data="tableDepot"
                  :cell-style="rowstyle"
                  :header-cell-style="headstyle"
                  height="200"
                  style="{'width': '100%','overflow-y': 'scroll'}"
                  :show-header="showHeader"
                >
                  <el-table-column prop="name" label="名称" :show-overflow-tooltip="true"></el-table-column>
                  <el-table-column prop="address" label="地址" :show-overflow-tooltip="true"></el-table-column>
                </el-table>
              </el-tab-pane>
            </el-tabs>
          </div>
        </div>
      </el-collapse-transition>
    </div>
  </div>
</template>

<script>
// 资源请求路径
// import { queryListDict, queryResGoods, queryTeam, queryDepot } from '../../api/resources'
import AddResources from '../AddResources' // 添加已调度资源组件
import TimeLine from '../../components/TimeLine'  // 时间线
import { builderParams } from '@/utils/builderParams'
import { post, get } from '@/utils/request'
// import {queryResGoods} from '../../api/resources'
// import {queryTeam} from '../../api/resources'
// import {queryDepot} from '../../api/resources'
export default {
  data () {
    return {
      input5: '',
      show: true,
      shows: true,
      rotate: false,
      seachshow: false,
      tabPosition: 'left',
      present: 0,
      tableMaterial: [],
      tableExpert: [],
      tableTeam: [],
      tableDepot: []
    }
  },
  watch: {
    present (value) {
      if (value === 0) {
        this.loadExpert()
      }
      if (value === 1) {
        this.loadMaterial()
      }
      if (value === 2) {
        this.loadTeam()
      }
      if (value === 3) {
        this.loadDepot()
      }
    }
  },
  methods: {

    // 事件回溯
    eventLine () {
      let id = this.$layer.iframe({
        content: {
          content: TimeLine, // 传递的组件对象
          parent: this, // 当前的vue对象
          data: { eventId: this.$parent.eventId }// props
        },
        shade: false, // 是否显示遮罩
        area: ['600px', '620px'],
        offset: ['700', '350'],
        title: '事件回溯'
      })
      this.layerId = id
    },
    // 添加已调度资源
    addresources (type, rows) {
      let id = this.$layer.iframe({
        content: {
          content: AddResources, // 传递的组件对象
          parent: this, // 当前的vue对象
          data: { type: type, row: rows }// props
        },
        shade: false, // 是否显示遮罩
        area: ['700px', '600px'],
        offset: ['960', '520'],
        title: '添加调度资源'
      })
      this.layerId = id
    },
    // find
    output () {
      if (this.present === 0) {
        this.loadMaterial(this.input5)
      }
      if (this.present === 1) {
        this.loadExpert(this.input5)
      }
      if (this.present === 2) {
        this.loadTeam(this.input5)
      }
      if (this.present === 3) {
        this.loadDepot(this.input5)
      }
    },
    // changepresent
    handleClick (tab, event) {
      if (tab.label === '物资') {
        this.present = 0
      } else if (tab.label === '专家') {
        this.present = 1
      } else if (tab.label === '队伍') {
        this.present = 2
      } else if (tab.label === '仓库') {
        this.present = 3
      }
    },
    // 加载物资
    async loadMaterial (value) {
      this.tableMaterial = []
      const table = {
        goodsName: value || '',
        id: '',
        resTypeCode: '',
        storeDeptId: ''
      }
      const configuration = await get({ url: 'static/config.json', data: {} })
      const queryResGoods = configuration.resource.baseUrl + configuration.resource.queryResGoods
      let expertResult = await get({ url: queryResGoods, data: table })
      if (expertResult.context.status === 200 && expertResult.data.totalItems > 0) {
        for (let i = 0; i < expertResult.data.resultList.length; i++) {
          this.$set(this.tableMaterial, i, {
            usable: expertResult.data.resultList[i].storeNum + expertResult.data.resultList[i].unit,
            address: expertResult.data.resultList[i].goodsName,
            goodsType: expertResult.data.resultList[i].resTypeCode,
            goodsNum: expertResult.data.resultList[i].storeNum,
            goodsSpec: expertResult.data.resultList[i].goodsSpec,
            stort: expertResult.data.resultList[i].address
          })
        }
        // expertResult.data.resultList
      }
    },
    // 加载专家
    async loadExpert (value) {
      this.tableExpert = []
      const table = {
        expertName: value || '',
        id: ''
      }
      const configuration = await get({ url: 'static/config.json', data: {} })
      const queryListDict = configuration.resource.baseUrl + configuration.resource.queryListDict
      let expertResult = await get({ url: queryListDict, data: table })
      if (expertResult.context.status === 200 && expertResult.data.totalItems > 0) {
        for (let i = 0; i < expertResult.data.resultList.length; i++) {
          this.$set(this.tableExpert, i, {
            name: expertResult.data.resultList[i].expertName,
            expertCode: expertResult.data.resultList[i].expertCode,
            telephone: expertResult.data.resultList[i].telephone,
            education: expertResult.data.resultList[i].education,
            email: expertResult.data.resultList[i].email
          })
        }
        // expertResult.data.resultList
      }
    },
    // 加载team
    async loadTeam (value) {
      this.tableTeam = []
      const table = {
        teamName: value || ''
      }
      const configuration = await get({ url: 'static/config.json', data: {} })
      const queryTeam = configuration.resource.baseUrl + configuration.resource.queryTeam
      let expertResult = await get({ url: queryTeam, data: table })
      if (expertResult.context.status === 200 && expertResult.data.totalItems > 0) {
        for (let i = 0; i < expertResult.data.resultList.length; i++) {
          this.$set(this.tableTeam, i, {
            name: expertResult.data.resultList[i].teamName,
            teamNum: expertResult.data.resultList[i].teamNum,
            header: expertResult.data.resultList[i].header,
            teamType: expertResult.data.resultList[i].teamCode,
            headerTel: expertResult.data.resultList[i].headerTel
          })
        }
        // expertResult.data.resultList
      }
    },
    // 加载depot 仓库
    async loadDepot (value) {
      this.tableDepot = []
      const table = {
        storeName: value || '',
        storeCode: '',
        areaId: '',
        id: ''
      }
      const configuration = await get({ url: 'static/config.json', data: {} })
      const queryDepot = configuration.resource.baseUrl + configuration.resource.queryDepot
      let expertResult = await get({ url: queryDepot, data: table })
      if (expertResult.context.status === 200 && expertResult.data.totalItems > 0) {
        for (let i = 0; i < expertResult.data.resultList.length; i++) {
          this.$set(this.tableDepot, i, {
            name: expertResult.data.resultList[i].storeName,
            address: expertResult.data.resultList[i].address
          })
        }
        // expertResult.data.resultList
      }
    },
    // 左右伸缩
    lefttelescopic () {
      this.show = !this.show
    },
    // 资源查询伸缩
    resrouceseach () {
      this.seachshow = !this.seachshow
    },
    resrouceseach1 () {
      if (this.seachshow === true) {
        this.seachshow = !this.seachshow
      }
    },
    // 旋转
    rotates () {
      this.rotate = !this.rotate
    },
    // 测试map定点
    setViewCenter (x, y, level) {
      // this.$map.clearLayers(this.$map.constslayer.alarmLayer)
      this.$map.setViewCenter({ x: x, y: y, level: level })
    },
    // clearLayers清除地图
    clearLayers () {
      this.$map.clearLayers()
    },
    // 路径规划
    mapPath () {
      const vm = this
      this.$map.drawGraphic({
        url: '#',
        mode: 1, //  1 point, 2 line, 3 polygon, 4 rectangle, 9 circle
        isWindow: false,
        isCenter: false,
        layerId: 'glayer',
        click: '',
        ico: { name: './static/arcgismap/assets/img/car.png', height: 30, width: 31 },
        onsuccess: function (wkt, e) {
          vm.getPath(wkt)
        }
      })
      // this.$map.drawLine({
      //   url: '#',
      //   pointlist: 'LINESTRING(117.20096071123281 39.22406841585012, 117.19911281091346 39.224364808227385, 117.19919865487773 39.22519558455154, 117.19571757513415 39.2248747453248, 117.19614751114663 39.223695868196806, 117.19552434052898 39.22352878288884, 117.20227214311147 39.217740340046824, 117.2021860565214 39.218886622274184, 117.20214301418827 39.21945144945228, 117.20368994289551 39.21928740857768, 117.20390469863345 39.21996887780237)', // // 也可以是WKT
      //   isCenter: false,
      //   isWindow: false,
      //   layerId: 'glayer',
      //   click: '',
      //   onsuccess: '',
      //   style: {
      //     color: '#00FF7F',
      //     fontSize: 12,
      //     lineStyle: 'solid',
      //     opacity: 0.8,
      //     borderWidth: 1,
      //     borderColor: '#00FF7F',
      //     // background: '#00FF7F',
      //     fillStyle: 'solid'
      //   }
      // })
      // this.$map.drawPointToMap({
      //   x: 117.20390469863345,
      //   y: 39.21996887780237,
      //   ico: { name: './static/arcgismap/assets/img/marker.png', height: 30, width: 22 },
      //   layerId: this.$map.constslayer.mapPath,
      //   isCenter: false,
      //   click: () => { }
      // })
    },
    async getPath (wkt) {
      // 根据选中的点，规划路线
      let startPoint = ''
      let endPoint = ''
      const configuration = await get({ url: 'static/config.json', data: {} })
      let conditions = [{ Property: 'Id', Oper: 'eq', Value: this.$parent.eventId }]
      let item = builderParams.get('Event', '', conditions)
      item.DataBase = 'duty'
      const result = await post({ url: builderParams.getUrl(), data: item })
      if (result.Result === 0) {
        if (result.Data.length > 0) {
          endPoint = result.Data[0].Dwxzb + ',' + result.Data[0].Dwyzb
        }
      }
      startPoint = wkt.substring(wkt.indexOf('(') + 1, wkt.length - 1).replace(' ', ',')
      // 获取路径规划接口地址
      let MapPlanUrl = configuration.Map.MapPlan.replace('{startPoint}', startPoint).replace('{endPoint}', endPoint)
      let pointData = await get({ url: MapPlanUrl, data: {} })
      let pointList = pointData.TransAnalystInfo.pathAnalystCoords
      let MapLine = 'LINESTRING('
      pointList.forEach(element => {
        MapLine += element.x + ' ' + element.y + ','
      });
      MapLine = MapLine.substring(0, MapLine.length - 1)
      MapLine += ')'
      // 规划路线
      this.$map.drawLine({
        pointlist: MapLine,
        click: '',
        onsuccess: '',
        layerId: this.$map.constslayer.mapPath,
        style: {
          color: '#00FF7F',
          fontSize: 12,
          lineStyle: 'solid',
          opacity: 0.8,
          borderWidth: 1,
          borderColor: '#00FF7F',
          background: '#00FF7F',
          fillStyle: 'solid'
        }
      })
      this.setViewCenter(startPoint.split(',')[0], startPoint.split(',')[1], 14)
    },
    headstyle () {
      return 'background-color: transparent; border: 0; color: #00f1e5; text-align: center;  background-color: #17649a;'
    },
    rowstyle ({ row, rowIndex }) {
      return 'background-color: transparent; border: 0; color: #88d6ff; text-align: center; background-color: #1c6190'
    },
    cellstyle () {
      return 'background-color: #17649a'
    },
    headerrowstyle () {
      return 'background-color: #17649a'
    }
  },
  mounted () {
    this.loadMaterial()
    this.loadExpert()
  }
}
</script>
<style scoped>
/* element ui样式 */
/* input的样式 */
.el-input__inner {
  -webkit-appearance: none;
  background-color: rgba(61, 103, 160, 0.6) !important;
  background-image: none;
  border-radius: 4px;
  border: 1px solid rgba(61, 103, 160, 0.6) !important;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  color: #606266;
  display: inline-block;
  font-size: inherit;
  height: 40px;
  line-height: 40px;
  outline: 0;
  padding: 0 15px;
  -webkit-transition: border-color 0.2s cubic-bezier(0.645, 0.045, 0.355, 1);
  transition: border-color 0.2s cubic-bezier(0.645, 0.045, 0.355, 1);
  width: 100%;
}

.el-input-group__append,
.el-input-group__prepend {
  background-color: #1f97e9 !important;
  color: #fff;
  vertical-align: middle;
  display: table-cell;
  position: relative;
  border: 1px solid #1f97e9 !important;
  border-radius: 4px;
  padding: 0 20px;
  width: 1px;
  white-space: nowrap;
}

.el-tabs__nav-scroll {
  width: 94px;
  overflow: hidden;
}

.el-tabs__item.is-active {
  color: #ebbb2f;
  background: #234a76 !important;
}

.el-tabs__item {
  padding: 0 34px;
  height: 40px;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  line-height: 40px;
  display: inline-block;
  list-style: none;
  font-size: 14px;
  font-weight: 500;
  color: #7ec8ed;
  position: relative;
}
.el-tabs__nav-wrap::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 2px;
  /* background-color: #E4E7ED; */
  z-index: 1;
}

.has-gutter {
  background: black !important;
}
/* tab左侧背景色 */
.el-tab-pane {
  height: 200px;
  background: #1f436d !important;
  color: #fff;
}
.el-tabs__content {
  overflow: hidden;
  position: relative;
  overflow-y: scroll !important;
}
.wrapper .el-table th,
.el-table tr {
  background-color: black !important;
}
.el-table__body {
  background-color: black !important;
}
</style>
<style lang="stylus">
.map {
  // width: 100%;
  // height: 100%;
  position: relative;

  // background: red;
  .map-tool {
    width: 614px;
    height: 70px;
    position: absolute;
    top: 22%;
    left: -38%;

    .map-gj {
      width: 68px;
      height: 68px;
      border-radius: 50%;
      background: url('../../assets/images/home/map-gj-quan.png') no-repeat;
      float: right;
      position: absolute;
      top: -20%;
      left: 83%;
      cursor: pointer;
    }

    .aa {
      transition: all 1s;
    }

    .go {
      transform: rotate(-360deg);
      transition: all 1s;
    }

    .shensuo-box {
      float: right;
      width: 568px;
      height: 52px;
      background: url('../../assets/images/home/map-gj-fang.png') no-repeat;
      margin: -3px 86px 0px 0px;

      .text {
        width: 564px;
        height: 52px;
        margin: -18px 0 0 2px;

        span {
          color: #26befe;
          margin: 34px 0 0 20px;
          display: inline-block;
          cursor: pointer;
        }

        span:nth-child(2) {
          margin: 34px 0 0 44px;
        }

        span:nth-child(3) {
          margin: 34px 0 0 48px;
        }

        span:nth-child(4) {
          margin: 34px 0 0 44px;
        }

        span:nth-child(5) {
          margin: 0px 0 0 52px;
        }
      }
    }

    .fade-enter-active, .fade-leave-active {
      width: 568px;
      transition: width 1s;
    }

    .fade-enter, .fade-leave-to { /* .fade-leave-active below version 2.1.8 */
      width: 0px;
      transition: width 1s;
    }
  }

  // 资源查询
  .seach {
    position: absolute;
    // top: 76%;
    // left: -20%;
    margin: 56px 0 0 85px;

    .res-seach {
      // width: 356px;
      height: 430px;
      background: url('../../assets/images/home/ziyuan-reach.png') no-repeat;
      padding: 20px 20px 20px 20px;
      background-size: 490px 300px;

      .sea-input {
        width: 450px;
        height: 50px;
      }
    }
  }
}

.el-tabs__item {
  color: #409EFF !important;
}

.el-tabs__item.is-active {
  color: #ebbb2f !important;
}

.el-input__inner {
  color: #ffffff !important;
}
</style>



