<!-- 顶部个人信息 退出登录 组件-->
<template>
  <!-- 头部组件 -->
  <header class="tj-header">
    <div class="header-img">
    </div>
    <div class="rightbox">
      <div class="center">
        <ul>
        </ul>
      </div>
      <div class="right">
        <ul>
          <li>
            <!-- agent -->
            <div class="agent-icon"></div>
            <label class="gn-text" @mouseenter="Unfold1()" @mouseleave="Unfold1()">
              <!-- <el-badge :value="agentItem.length" class="item">代办</el-badge> -->
              <collapse>
                <div class="container1" v-show="isActive1">
                  <ul>
                    <li v-for="(item, index) in agentItem" :key="index">
                      <a href="javascript:;">
                        {{
                        item
                        }}
                      </a>
                    </li>
                  </ul>
                </div>
              </collapse>
            </label>
          </li>
          <li>
            <!-- user -->
            <div class="user-icon"></div>
            <label
              class="gn-text el-dropdown-link" @mouseenter=" Unfold2()" @mouseleave=" Unfold2()">
              <label>{{personName}}</label>
              <collapse>
                <div class="container2" v-show="isActive2">
                  <ul>
                    <li>
                      <a href="javascript:;">个人信息</a>
                    </li>
                    <!-- <li>
                      <a href="javascript:;">退出登入</a>
                    </li> -->
                  </ul>
                </div>
              </collapse>
            </label>
          </li>
          <li>
            <!-- 功能 -->
            <div class="gongneng" @mouseenter="Unfold()" @mouseleave="Unfold()">
              <div
                class="gn-icon"
                :class="[rotate ? 'fa fa-arrow-down go' : 'fa fa-arrow-down aa']"
              ></div>
              <collapse>
                <div class="container" v-show="isActive">
                  <ul>
                    <li v-for="(item, index) in link" :key="index">
                      <a @click="openWindow(item.url)" href="javascript:;">
                        {{
                        item.name
                        }}
                      </a>
                    </li>
                  </ul>
                </div>
              </collapse>
            </div>
            <label class="gn-text" @mouseenter="Unfold()" @mouseleave="Unfold()">功能</label>
          </li>
        </ul>
         <!-- 天气 -->
        <div class="weather">
          <div class="weather-top">
            <span class="duoyun"></span>
            <label>22℃</label>
          </div>
          <div class="weather-bottom">
            <label class="text">多云转晴</label>
            <label class="num">19~26℃</label>
          </div>
        </div>
      </div>
    </div>
  </header>
</template>

<script>
import ShrinkView from '../ShrinkView'
// import Element from '../../assets/js/index'
import collapse from '../../assets/js/collapse.js'
import { cachedata } from  '@/utils/cachedata'
export default {
  components: {
    ShrinkView,
    collapse
  },
  data () {
    return {
      open: false,
      isActive: false,
      isActive1: false,
      isActive2: false,
      rotate: false,
      link: [
        // { name: '总结评估', url: 'http://1.119.195.59:89/zhjy/Pages/default.html?urlKey=zjpg' },
        // { name: '指挥体系', url: 'http://1.119.195.59:89/zhjy/Pages/default.html?urlKey=zjpg' }
         { name: '总结评估', url: 'http://1.119.195.59:89/yjjy/appraiserweb/pages/default.html' },
        { name: '指挥体系', url: 'http://1.119.195.59:89/yjjy/schedulingweb/pages/default.html' }
      ],
      agentItem: [],
      // ['创业孵化基地发生紧急事件', '北城科技园发生火灾', '产业园发生紧急事件', '北城科技园发生火灾']
      weather: [{ value: '晴 25~30°', url: require('../../assets/images/home/sun.png') }, { value: '4~6级西北风', url: require('../../assets/images/home/wind.png') }, { value: '湿度 80%', url: require('../../assets/images/home/humidity.png') }, { value: '气压 50kPa', url: require('../../assets/images/home/airpressure.png') }],
      personName: '李四'
    }
  },
  methods: {
    // 展开折叠
    Unfold () {
      this.isActive = !this.isActive
    },
    Unfold1 () {
      this.isActive1 = !this.isActive1
    },
    Unfold2 () {
      this.isActive2 = !this.isActive2
    },
    start () {
      this.rotate = !this.rotate
    },
    openWindow (url) {
      window.open(url)
    },
    loadCache () {
      const loginInfo = cachedata.getLogin()
      if (loginInfo) {
        this.personName = loginInfo.Profile.UserName
      }
    },
  },
  mounted () {
    this.loadCache()
  }
}
</script>

<style lang="stylus" scoped>
@import '../../assets/stylus/mixins.styl';

.tj-header {
  width: 100%;
  height: 10%;
  background: rgba(255, 255, 255, 0);
  display: flex;
  justify-content: space-between;
  align-items: center;
  pointer-events: auto;
  position: relative;
  background: url('../../assets/images/home/bgheader.png') no-repeat;

  .header-img {
    width: 25%;
    height: 100%;
    // background : url("../../assets/images/home/emergency.png") no-repeat center 15px;
    // background-size : 120%;
  }

  .selectbox {
    position: absolute;
    left: 84%;
    top: 3%;
    width: 276px;
  }

  .rightbox {
    display: flex;
    width: 75%;
    height: 80%;
    float: right;
    margin-top: -20px;
    // background : url("../../assets/images/home/backgroundbanner.png") no-repeat;
    // background-size : cover;
  }

  .center {
    width: 60%;
    height: 100%;
    margin-left: 5%;
    // background : #00ffff
  }

  .center ul {
    width: 100%;
    height: 100%;
    display: flex;
  }

  .center ul>li {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 25%;
    height: 100%;
    box-sizing: border-box;
    // border 1px solid red;
    // background : #ff8040
  }

  .center ul>li p {
    width: 20%;
  }

  .center ul>li img {
    // width : 80%;
    height: auto;
  }

  .center ul>li span {
    margin-left: 10px;
    color: #0a9bad;
    // font-size : 10%;
  }

  .right {
    margin-left: 20px;
    width: 40%;
    height: 100%;
    position relative
    // background : #ff0080
  }

  .right>ul {
    width: 100%;
    height: 100%;
    display: flex;
  }

  .right>ul>li {
    width: 24%;
    height: 100%;
    display: flex;
    position: relative;
    z-index: 999;
    align-items: center;
    justify-content: center;
    // background : #3f6281;
    box-sizing: border-box;
    // border 1px solid red;
  }

  .right ul>li:hover {
    // background : red
  }

  .zqq {
    i {
      display: block;
      width: 20px;
      height: 20px;
      background: red;
    }

    .aa {
      transition: all 1s;
    }

    .go {
      transform: rotate(-180deg);
      transition: all 1s;
    }
  }

  .agent-icon {
    width: 38px;
    height: 38px;
    background: url('../../assets/images/home/bell.png') no-repeat 0px 10px;
    cursor: pointer;
  }

  .user-icon {
    width: 38px;
    height: 38px;
    background: url('../../assets/images/home/user.png') no-repeat 0px 10px;
    cursor: pointer;
    lable {
      cursor pointer
    }
  }

  .gongneng {
    // position: absolute;
    // left: 86.5%;
    // top: 20%;
    // z-index: 1;
    .gn-icon {
      width: 38px;
      height: 38px;
      background: url('../../assets/images/home/gn.png') no-repeat 0px 10px;
      cursor: pointer;
    }

    .aa {
      transition: all 1s;
    }

    .go {
      transform: rotate(-360deg);
      transition: all 1s;
    }

    .container {
      width: 124px;
      color: #fff;
      position: absolute;
      left: 0;
      top: -40px;
      background: rgba(9, 80, 140, 0.7);
      // margin: 12px 0 0 6px;
      // padding: 12px 0px 12px 12px;
      margin-top: 100px;
      border-radius: 10px;

      ul li {
        height: 40px;
        line-height: 40px;
        text-align: center;
      }

      ul li a:hover {
        color: #ccc;
      }
    }
  }
  // 天气
  .weather {
    display block
    width 100%
    height 100%
    position: absolute;
    left: 72%;
    top: 18%;
    .weather-top {
      width 120px
      height 40px
      text-align center
      // background red
      .duoyun {
        display inline-block
        width 33px
        height 26px
        background: url('../../assets/images/home/duoyun.png') no-repeat;
      }
      label {
        color #37908B;
      }
    }
    .weather-bottom {
      width 140px
      height 40px
      color #37908B;
      .text {

      }
    }
  }
  .container1 {
    width: 250px;
    color: #fff;
    position: absolute;
    background: rgba(9, 80, 140, 0.7);
    left: -10px;
    top: 60px;
    font-size: 16px;
    border-radius: 10px;

    ul li {
      height: 40px;
      line-height: 40px;
      text-align: center;
    }

    ul li a:hover {
      color: #ccc;
    }
  }

  .container2 {
    width: 124px;
    color: #fff;
    position: absolute;
    background: rgba(9, 80, 140, 0.7);
    left: 25px;
    top: 60px;
    font-size: 16px;
    border-radius: 10px;

    ul li {
      height: 40px;
      line-height: 40px;
      text-align: center;
    }

    ul li a:hover {
      color: #ccc;
    }
  }

  .gn-text {
    width: 44px;
    font-size: 20px;
    color: #37908B;
    // position: absolute;
    // top: 10px;
    // left: 48px;
    cursor: pointer;
  }
}
</style>



