import Vue from 'vue'
import App from './App.vue'
import router from './router'

import layer from 'vue-layer'
import 'vue-layer/lib/vue-layer.css'
import '@/assets/css/layer/common.css'

import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'

import mapHandle from '@/utils/mapHandle'

import creWebSocket from '@/utils/creWebSocket'

// 自定义的组件 注册全局组件
import Maptools from '@/components/Maptools' // 地图工具组件
import Timetools from '@/components/Timetools' // 时间组件
import Title from '@/components/Title' // 标题组件

Vue.use(Maptools)
Vue.use(Timetools)
Vue.use(Title)

Vue.component('map-tool', Maptools) // 初始化地图工具组件
Vue.component('time-tool', Timetools) // 初始化地图工具组件
Vue.component('v-title', Title) // 初始化地图工具组件

creWebSocket.init({
  xzqh: '120000',
  unit: '120000000000',
  person: '0001'
})

Vue.prototype.$ws = creWebSocket

Vue.prototype.$map = mapHandle

Vue.prototype.$layer = layer(Vue)

Vue.config.productionTip = false

Vue.use(ElementUI)

// eslint-disable-next-line no-new
new Vue({
  el: '#app',
  router,
  components: {
    Maptools,
    Timetools,
    Title
  },
  render: h => h(App)
})



