// eslint-disable-next-line no-unused-vars
const session = {
  setItem (name, value) {
    window.sessionStorage.setItem(name, value)
  },
  getItem (name) {
    return window.sessionStorage.getItem(name)
  },
  removeItem (name) {
    window.sessionStorage.removeItem(name)
  }
}

const cachedata = {
  setToken (token) {
    session.setItem('token', token)
  },
  getToken () {
    var token = session.getItem('token')
    if (token == null || token === '') {
      return ''
    } else {
      return token
    }
  },
  writeUserSession (user) {
    var obj = JSON.stringify(user)
    session.setItem('User', obj)
  },
  getLogin () {
    var users = session.getItem('User')
    if (users == null || users === '') {
      return ''
    } else {
      return JSON.parse(users)
    }
  }
}

export { session, cachedata }



