import { session } from '@/utils/cachedata'
// import { getConfigData } from '@/api/login'

const config = {
  loadConfig: function (callback) {
    var result = {}
    result.centerUrl = 'http://1.119.195.59:8088'// 配置中心地址
    result.id = 'zhdd'// 业务编号（对应业务配置编号）
    result.name = '指挥调度'// 业务名称
    result.terminal = 'web'// 应用方式
    result.mqConf = { // mq配置
      rabbitmqServer: '1.119.195.59:7002',
      rabbitmqUser: 'zhdd',
      rabbitmqPass: 'zhdd',
      appid: 'zhdd'
    }
    if (process.env.NODE_ENV === 'production') {
      result.centerUrl = 'http://1.119.195.59:8088'
      // result.mqConf.rabbitmqServer = '14.121.22.234:15674'
      result.mqConf.rabbitmqServer = '1.119.195.59:8088'
    }
    if (typeof callback === 'function') {
      callback() // 成功后回调函数
    }
    return result
  },
  // 获取配置中心的配置
  getConfig: function () {
    return config.loadConfig()
  }
}

const businessConfig = {
  async requestConfig (key, callback) {
    const data = {
      key: key
    }
    const res = await getConfigData(data)
    if (res.Result === 0) {
      var obj = JSON.stringify(res.Data)
      session.setItem(key + '_dbconfig', obj)
      if (typeof callback === 'function') {
        callback() // 成功后回调函数
      }
    }
  },
  loadConfig: function (callback) {
    var item = config.getConfig()
    var checkdbconfig = businessConfig.getConfig()
    if (checkdbconfig === '') { // 配置信息为空
      if (item !== '') {
        businessConfig.requestConfig(item.id, callback)
      } else {
        if (typeof callback === 'function') {
          callback() // 成功后回调函数
        }
      }
    } else {
      if (typeof callback === 'function') {
        callback() // 成功后回调函数
      }
    }
  },
  getConfig () {
    var item = config.getConfig()
    var dbconfig = session.getItem(item.id + '_dbconfig')
    if (dbconfig === null || dbconfig === '') {
      // return ''
      // 因暂时没有登录，缓存没有相关配置
      return { Serviceurl: config.loadConfig().centerUrl + '/commonweb/api' }
    } else {
      return JSON.parse(dbconfig)
    }
  }
}

export { config, businessConfig }



