              >
                <el-form-item label="企业代表人">
                  <el-input
                    v-model="formHelpEnterprise.businessRepresentative"
                    placeholder="请输入企业代表人"
                  ></el-input>
                </el-form-item>
                <el-form-item label="企业名称">
