<script>
import { builderParams } from '@/utils/builderParams'
import { post, get } from '@/utils/request'
// import { eventResourceAllocate, eventRescueTeamLocation, eventRescueCar } from '../../api/visual'
export default {
  data () {
    return {
      curr: '0',
      tabPosition: 'left',
      labelPosition: 'right',
      formLabelAlign: {
        name: '',
        personcharge: '',
        address: '',
        emile: '',
        phone: ''
      },
      // 处置队伍
      formDisposalTeam: {
        name: '',
        number: '',
        personcharge: '',
        teamType: '',
        phone: ''
      },
      // 调用物资
      formInvokeMaterial: {
        name: '',
        type: '',
        number: '',
        model: '',
        warehouse: '',
        people: ''
      },
      // 专家
      formExpert: {
        name: '',
        type: '',
        fullname: '',
        phone: '',
        education: '',
        emile: ''
      },
      // 车辆
      formCars: {
        carnumber: '',
        type: '',
        zbwz: '',
        driver: '',
        phone: ''
      },
      // 救援企业
      formHelpEnterprise: {
        businessRepresentative: '',
        name: '',
        address: '',
        type: '',
        phone: '',
        respectiveRegion: '',
        emile: ''
      }
    }
  },
  props: {
    type: {
      type: String,
      default: ''
    },
    row: {
      type: Object,
      default: {}
    }
  },
  methods: {
    getuuid () {
      return ((((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1) + (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1) + '' + (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1) + '' + (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1) + '' + (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1) + '' + (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1) + (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1) + (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1))
    },
    // 获取当前时间
    getNowTime () {
      return new Date(+new Date() + 8 * 3600 * 1000).toISOString().replace(/T/g, ' ').replace(/\.[\d]{3}Z/, '')
    },
    async addSource (type) {
      let eventId = this.$parent.eventId
      if (!eventId) {
        eventId = this.$parent.$parent.eventId
      }
      const configuration = await get({ url: 'static/config.json', data: {} })

      // 投大屏接口 参数
      var param = { id: eventId, allocateContent: '' }
      // 添加已调读资源 组织机构(jg),处置队伍(team)，调用物资(goods), 专家(expert), 车辆(car), 救援企业(company)
      let table = {}
      if (type === 'team') {
        param.allocateContent = '调度队伍：' + this.formDisposalTeam.name
        let TableData = {
          id: this.getuuid(),
          // 事件编号
          eventId: eventId,
          // 队伍编号
          ranksId: '',
          // 队伍名称
          ranksName: this.formDisposalTeam.name,
          // 队伍类型
          ranksType: this.formDisposalTeam.teamType,
          // 队伍描述
          ranksDesc: '',
          // 负责人
          person: this.formDisposalTeam.personcharge,
          // 联系电话
          tel: this.formDisposalTeam.phone,
          // 是否调度 0否 1 是
          Dispatch: 1,
          // 调度时间
          DispatchTime: this.getNowTime(),
          // 入库时间
          CreateTime: this.getNowTime(),
          // 是否预案匹配  0否
          Matchimg: 0,
          // 邮箱
          email: '',
          // 队伍人数
          personNumber: this.formDisposalTeam.number
        }
        table = builderParams.save('ranks', TableData)
      }
      if (type === 'goods') {
        param.allocateContent = '调度物资：' + this.formInvokeMaterial.name + ', 数量：' + this.formInvokeMaterial.number

        let item = {
          id: this.getuuid(),
          eventId: eventId,
          // 资源类型
          restype: this.formInvokeMaterial.type,
          // 资源名称
          resName: this.formInvokeMaterial.name,
          // 调度数量
          dispatchCount: this.formInvokeMaterial.number,
          // 规格型号
          specification: this.formInvokeMaterial.model,
          // 所属仓库
          warehouse: this.formInvokeMaterial.warehouse,
          // 操作人
          operator: this.formInvokeMaterial.people,
          // 调度时间
          dispatchTime: this.getNowTime(),
          // 入库时间
          createTime: this.getNowTime(),
          // 是否预案匹配
          matching: 0
        }
        table = builderParams.save('RESOURE', item)
      }
      if (type === 'expert') {
        param.allocateContent = '调度专家：' + this.formExpert.name

        let item = {
          id: this.getuuid(),
          eventId: eventId,
          // 专家类型
          expertType: this.formExpert.type,
          // 专家名称
          expertName: this.formExpert.name,
          // 联系电话
          tel: this.formExpert.phone,
          // 学历
          education: this.formExpert.education,
          // 邮箱
          email: this.formExpert.emile,
          // 是否预案匹配
          matching: 0,
          // 是否调度
          dispatch: 1,
          // 调度时间
          dispatchTime: this.getNowTime(),
          // 入库时间
          createTime: this.getNowTime()
        }
        table = builderParams.save('exper', item)
      }
      if (type === 'car') {
        param.allocateContent = '调度车辆：' + this.formCars.carnumber

        let item = {
          id: this.getuuid(),
          eventId: eventId,
          carId: this.formCars.carnumber,
          // 车牌号
          carName: this.formCars.carnumber,
          // 车辆类型
          carsType: this.formCars.type,
          // 装备物资
          goods: this.formCars.zbwz,
          // 驾驶员
          cardriver: this.formCars.driver,
          // 电话
          tel: this.formCars.phone,
          // 调度时间
          dispatchTime: this.getNowTime(),
          // 入库时间
          createTime: this.getNowTime(),
          // 是否调度
          dispatch: 1,
          // 是否预案匹配
          matching: 0
        }
        table = builderParams.save('car', item)
      }
      if (type === 'company') {
        param.allocateContent = '调度机构：' + this.formHelpEnterprise.name

        let item = {
          id: this.getuuid(),
          eventId: eventId,
          // 企业名称
          enterpname: this.formHelpEnterprise.name,
          // 地址
          enterpAddress: this.formHelpEnterprise.address,
          // 企业代表人
          enterpdelegate: this.formHelpEnterprise.businessRepresentative,
          // 业务类型
          enterpType: this.formHelpEnterprise.type,
          // 联系电话
          tel: this.formHelpEnterprise.phone,
          // 所属区域
          area: this.formHelpEnterprise.respectiveRegion,
          // 邮箱
          email: this.formHelpEnterprise.emile,
          // 是否预案匹配
          matching: 0,
          // 是否调度
          dispatch: 1
        }
        table = builderParams.save('enterp', item)
      }
      if (table !== {}) {
        const result = await post({ url: builderParams.execUrl(), data: table })
        if (result.Result === 0) {
          const eventResourceAllocate = configuration.visual.baseUrl + configuration.visual.eventResourceAllocate
          // 请求可视化交互系统对外接口  投大屏
          var quartzItem = {
            // 编号
            bh: this.getuuid(),
            // 系统名称
            xtmc: 'zhdd',
            // 调用地址
            jkdz: eventResourceAllocate,
            // 调用类型
            ajaxType: 'POST',
            // 请求参数
            param: JSON.stringify(param),
            // 状态
            state: 0,
            // 入库时间戳
            rksjc: this.getNowTime(),
            // 更新时间
            gxsj: null,
            // 操作人编号
            gxrbh: '',
            // 操作人
            gxr: '',
            header: '{"Authorization":"emer_test","Signature":"aedd64f062dba1ab2eb53c4977921586","Timestamp":"1574334059"}'
          }

          if (type !== 'team' && type !== 'car') {
            const task = builderParams.save('quartz', quartzItem)
            task.DataBase = 'duty'
            // 非队伍走 上报事件当前资源调配情况
            const taskResult = await post({ url: builderParams.execUrl(), data: task })
          }
          if (type === 'team') {
            // 队伍  上报事件关联救援队伍实时信息--多次推送 eventRescueTeamLocation
            const eventRescueTeamLocation = configuration.visual.baseUrl + configuration.visual.eventRescueTeamLocation
            quartzItem.jkdz = eventRescueTeamLocation
            var teamParmeter = {
              eventId: eventId,
              rescueTeamId: '',
              startLongitude: '',
              startLatitude: '',
              endLongitude: '',
              endLatitude: '',
              currentLongitude: '',
              currentLatitude: '',
              status: '行进中',
              remark: '测试1'
            }
            quartzItem.param = JSON.stringify(teamParmeter)
            let teamItem = builderParams.save('quartz', quartzItem)
            teamItem.DataBase = 'duty'
            const taskResult = await post({ url: builderParams.execUrl(), data: teamItem })
          }
          if (type === 'car') {
            // 上报事件关联救援车辆-多次推送
            const eventRescueCar = configuration.visual.baseUrl + configuration.visual.eventRescueCar
            quartzItem.jkdz = eventRescueCar
            var carParmeter = {
              eventId: eventId,
              plateNum: this.formCars.carnumber,
              type: this.formCars.type,
              // 所属单位
              org: '',
              content: this.formCars.zbwz,
              driver: this.formCars.driver,
              driverPhone: this.formCars.phone,
              startLongitude: '',
              startLatitude: '',
              endLongitude: '',
              endLatitude: '',
              currentLongitude: '',
              currentLatitude: '',
              status: '行进中',
              // 备注
              remark: ''
            }
            quartzItem.param = JSON.stringify(carParmeter)
            let carItem = builderParams.save('quartz', quartzItem)
            carItem.DataBase = 'duty'
            const taskResult = await post({ url: builderParams.execUrl(), data: carItem })
          }
          alert('添加成功！')
          // 右下角点击添加，刷新已调读资源列表
          if (this.$parent.eventId) {
            this.$parent.loadDispatch()
          }
          if (this.$route.name === 'command') {
            this.$parent.$parent.loadDispatch()
          }
          // 关闭当前组件
          this.$layer.closeAll()
        }
      }
    },
    loadinit () {
      if (this.type === 'goods') {
        this.curr = '1'
        this.formInvokeMaterial.name = this.row.address,
        this.formInvokeMaterial.type = this.row.goodsType,
        this.formInvokeMaterial.number = this.row.goodsNum,
        this.formInvokeMaterial.model = this.row.goodsSpec,
        this.formInvokeMaterial.warehouse = this.row.stort,
        this.formInvokeMaterial.people = '李铭'
      }
      if (this.type === 'expert') {
        this.curr = '2'
        this.formExpert.name = this.row.name,
        this.formExpert.type = this.row.expertCode,
        this.formExpert.fullname = this.row.name,
        this.formExpert.phone = this.row.telephone,
        this.formExpert.education = this.row.education,
        this.formExpert.emile = this.row.email
      }
      if (this.type === 'team') {
        this.curr = '0'
        this.formDisposalTeam.name = this.row.name
        this.formDisposalTeam.number = this.row.teamNum
        this.formDisposalTeam.personcharge = this.row.header
        this.formDisposalTeam.teamType = this.row.teamType
        this.formDisposalTeam.phone = this.row.headerTel
      }
    }
  },
  mounted () {
    this.loadinit()
  },
  components: {

  }

}
</script>
